import React, {useEffect, useState} from 'react'
import styles from './styles'
import Loader from '../Graphics/Loader'
// Redux
import {connect} from 'react-redux'
//import {change} from '../actions/ListRol'
import {change} from '../../Redux/actions/action_user'
import store from '../../Redux/store'
import ReactDOMServer from "react-dom/server";
import {getPositions,getDataEstados} from '../Opendata/data'

const googleMapScript = document.createElement('script')
let map_c;
let  loc = []

const GoogleMap =(props)=> {

    const googleMapRef = React.createRef()
    //const [polyloc,setPolyloc] = useState(null)
    const [load,setLoad] = useState(false)
    const [namel,setNamel] = useState('')
    //const {polyLoc } = useMap();

    const changeLoc =(name)=> {
        props.change(['Localidad',name]);
        setNamel(name)
        console.log(namel)
        //console.log('changeLoc, ',name)
    }


    const InfoWindowContent=(name,estados)=>{
        
        return(
            <div className="row" style={{fontFamily: 'Lora', width: '400px',height: '180px', textAlign: 'center',fontSize:'20px'}}>
                <div className="col-md-12">
                    <h3> {name} </h3>
                </div>
                <div style={{textAlign: 'center'}} className="col-md-8">
                    Recuperado
                </div>
                <div style={{textAlign: 'center', backgroundColor: '#32a89d', borderRadius: '15px', color:'#ffffff'}} className="col-md-3">
                    {estados['Recuperado']}
                </div>
                <div style={{textAlign: 'center'}} className="col-md-8">
                    Fallecido
                </div>
                <div style={{textAlign: 'center', backgroundColor: '#c72020', borderRadius: '15px', color:'#ffffff'}} className="col-md-3">
                    {estados['Fallecido']}
                </div>
                <div style={{textAlign: 'center'}} className="col-md-8">
                    Grave
                </div>
                <div style={{textAlign: 'center',backgroundColor: '#f26863', borderRadius: '15px', color:'#ffffff'}} className="col-md-3">
                    {estados['Grave']}
                </div>
                <div style={{textAlign: 'center'}} className="col-md-8">
                    Moderado
                </div>
                <div style={{textAlign: 'center',backgroundColor: '#c7bc20', borderRadius: '15px', color:'#ffffff'}} className="col-md-3">
                    {estados['Moderado']}
                </div>
                <div style={{textAlign: 'center'}} className="col-md-8">
                    Leve
                </div>
                <div style={{textAlign: 'center',backgroundColor: '#6fcc43', borderRadius: '15px', color:'#ffffff'}} className="col-md-3">
                    {estados['Leve']}
                </div>
            </div>
        )
    }

    useEffect((props)=>{

        googleMapScript.src = `https://maps.googleapis.com/maps/api/js?key=AIzaSyAcpnxVkDPet7-hIIVQM0gBMzwfLqb2Jyk&callback=createGoogleMap`
        window.document.body.appendChild(googleMapScript)

        setTimeout(() => {
            googleMapScript.addEventListener('load', {
                googleMap: createGoogleMap(),
                marker: createMarker({ lat: 4.638743, lng: -74.085238 }),
                //Polygon: createShape(),
                Polygon: addShape(),
                //circle: createCircle()
            })
            //polyloc = store.getState().reducer_app.PolyLoc
        }, 3000);
    },[load])


    const createGoogleMap = () =>{
        setLoad(true)
        //props.get_positions()
        const google = window.google;
        let Map = new google.maps.Map(googleMapRef.current, {
            zoom: 10,
            center: {
                lat: 4.60971,
                lng: -74.08175,
            },
            styles: styles,
            disableDefaultUI: true,
        });
        map_c = Map
        return Map
    }

    const createMarker = (data) =>{
        //props.get_positions()
        //props.get_loc()
        const google = window.google;
        let icon = {
            url: "http://iconsetc.com/icons-watermarks/simple-purple/bfa/bfa_map-marker/bfa_map-marker_simple-purple_512x512.png", // url
            scaledSize: new google.maps.Size(50, 50),
        };
        let marker = new google.maps.Marker({
            position: data,
            //map: this.createGoogleMap(),
            map: map_c,
            icon: icon,
        })
        return marker;
    }

    const addShape = async ()=>{
        const Polygons = await getPositions()
        console.log('Polygons',Polygons)
        setTimeout(()=>{
            //var npolygon = store.getState().reducer_app.Polygons
            var npolygon = Polygons
            var danger = store.getState().reducer_app.Location
            let a = []
            //loc = store.getState().reducer_app.Location['Localidades']['peligrosidad']
            for( var j = 0 ; j < npolygon.length;j++){
                let color = danger['peligrosidad']
                console.log('danger: ',color)
                a.push(createShape(npolygon[j]['1'],npolygon[j]['0'],color[j]))
            }
            return a
        }, 4000)
    }


    const createShape = async (path,name,color) =>{
        const google = window.google;
        var addColor = 255 + color;
        var neighbor = new google.maps.Polygon({
            paths: path,
            strokeColor: '#147ef7',
            strokeOpacity: 0.8,
            strokeWeight: 2,
            //fillColor: `rgb(255,${255-color},0)`,
            fillColor: "rgb(255,"+addColor+",0)",
            fillOpacity: 0.5
        });

        let namel = name.toLowerCase();
        if (namel === 'candelaria') namel = 'la candelaria'
        let data = await getDataEstados()
        let localidades = data['localidades']
        let estados = data['Estados']
        console.log("data: ",data)
        let index = localidades.indexOf(namel)
        console.log("index: ",index,namel)
        console.log("estados ",estados)
        console.log("estados_ index:",estados[index])
        let estado_index = estados[index]

        neighbor.setMap(map_c);
        neighbor.addListener('click', showData);

        //let infoWindow = new google.maps.InfoWindow();
        const infowindow = new window.google.maps.InfoWindow({
            content: ""
        });

        function showData(event) {
            changeLoc(name)

            const content = ReactDOMServer.renderToString(InfoWindowContent(name,estado_index));
            infowindow.setContent(content);
            infowindow.setPosition(event.latLng);
            infowindow.open(map_c)
            /*infoWindow.setContent(dataNeigh);
            infoWindow.setPosition(event.latLng);
            infoWindow.open(map_c);*/
        }
        return neighbor.setMap(map_c)
    }

    const createCircle =()=>{
        const google = window.google;
        var circle = new google.maps.Circle({
            strokeColor: "#FF0000",
            strokeOpacity: 0.8,
            strokeWeight: 2,
            fillColor: "#FF0000",
            fillOpacity: 0.35,
            map: map_c,
            center: {lat: 4.638743, lng: -74.085238},
            radius: 10000
        });
        return circle
    }

    return (
        <div
            id="google-map"
            ref={googleMapRef}
            //style={{ width: '700px', height: '600px', borderRadius: '20px'}}
            style={{ width: '100%', height: '600px', borderRadius: '20px'}}
        >
            <div style={{paddingTop: '250px'}}>
                <Loader type="map"/>
            </div>
        </div>


    )
}

const mapStateToProps = (state) => {
    return {
       App: state.reducer_app
    };
};

const mapDispatchToProps = (dispatch) => {
    return{
        //get_positions: () => {dispatch(get_positions())},
        //get_loc: () => {dispatch(get_loc())},
        change: (name) => {dispatch(change(name))},

    }
}


export default (connect(mapStateToProps, mapDispatchToProps)(GoogleMap));

