import React,{useEffect, useState} from 'react';
import './App.css';

import {connect} from 'react-redux'
import Filters from './Components/Graphics/filters'
import GoogleMap from './Components/Map/map'
import Graphics from './Components/Graphics/graphic'
import Slider from './Components/Slider/slider'
import {use_all} from './Redux/actions/actions_app'


const App =(props)=> {

    const [date,setDate] = useState('7/07/2020')

    let {User} = props;
    let getDate = (date) => setDate(date) //console.log(date)
    const [loaded] = useState()

    useEffect(()=>{
        setTimeout(()=>{ props.use_all()},500)
    },[loaded])

    return (
        <div className="App">
            <div className="container-fluid">
                <div className="row">
                    <div className="col-md-6">
                        <div style={{paddingTop: '15px'}}>
                            <GoogleMap/>
                        </div>
                    </div>
                    <div className="col-md-6" style={{paddingTop:'15px'}}>
                        <Graphics localidadFilter={User.Localidad} dateFilter={date} />
                    </div>
                </div>
                <div style={{paddingTop:'20px'}} className="row">
                    <div className="col-md-6">
                        <Slider handler={getDate}/>
                    </div>
                    <div className="col-md-6"></div>
                </div>
            </div>
        </div>
    );
}

const mapStateToProps = (state) => {
    return {
        User: state.reducer_user,
        App: state.reducer_app
    };
};

const mapDispatchToProps = (dispatch) => {
    return{
        use_all: () => {dispatch(use_all())},
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(App)
