import React from 'react';

import {Pie} from 'react-chartjs-2';
import {getDataGeneros} from '../Opendata/data.js';
import Loader from './Loader'

export default class PieChart extends React.Component{
    constructor(props){
        super(props);
        this.state={
            dataIsLoaded: false,
            localidadFilter: this.props.localidadFilter,
            dateFilter: this.props.dateFilter,
            chartData:{
                title: 'Distribución de casos por genero',
                type: 'doughnut',
                labels: ['Masculino', 'Femenino'],
                datasets: [{
                  data: [0, 0],
                  backgroundColor: ['#47AEE9', '#F186B3'],
                  hoverBackgroundColor:['#47AEE6', '#F186B02'],
                }]
            }
        }
        //this.dataLoader = this.dataLoader.bind(this)
    }

    getData = async ()=>{
      //  console.log('chartprop',this.props.dateFilter)
        //var  datajson = await getDataGeneros(this.props.dateFilter ? this.props.dateFilter  : "1/07/2020"  )
        var  datajson = await getDataGeneros( this.props.dateFilter, this.props.localidadFilter )
        let char_data = this.state.chartData
        await char_data.datasets.map( dataset => { dataset.data = datajson; return null })
        this.setState({chartData: char_data})
        this.setState({dataIsLoaded: true})
    }

    componentDidMount(){
        console.log('dataIsLoaded',this.state.dataIsLoaded)
        this.getData()
    }


    componentDidUpdate(prevProps, prevState, snapshot){
      if ( (prevProps.dateFilter != this.props.dateFilter) || (prevProps.localidadFilter != this.props.localidadFilter) ){
        console.log('propsUpdateLocality prev:'+(prevProps.localidadFilter)+" this:"+this.props.localidadFilter);
        console.log('propsUpdateDate prev:'+(prevProps.dateFilter)+" this:"+this.props.dateFilter);
        this.setState({dataIsLoaded: false})
        this.getData()
       }
    }


    render() {
        return (
        <div className="row" style={{height:'0px',paddingTop: '20px'}}>
            <h5>{this.state.chartData.title}</h5>
            {
                !this.state.dataIsLoaded ? <Loader/>
                : <Pie data={this.state.chartData} />
            }

            <div className="col-md-10">

            </div>
        </div>
        );
    }

};
