
import React,{ Component} from 'react';

import {connect} from 'react-redux'


import PieChart from './PieChart.js';
import BarChart from './BarChart.js';
import LinesChart from './LinesChart.js';
import {casesState} from '../Opendata/staticData.js';

//import LinePieChart from './LineChart.js';

class Graphic extends Component{

    constructor(props){
        super(props);

        this.state={ };
        //console.log('props',this.props);
    }

    componentDidUpdate(){
      //console.log('props',this.props);
    }

    render(){
        return(
            <div className="row" style={{height:'250px',paddingTop: '20px'}}>
                <div className="col-md-12"> <h2> Distribución de casos por localidad </h2> </div>
                <div style={{paddingTop: '-10px'}} className="col-md-12"> <BarChart type='localidad'/></div>
                <div style={{paddingTop: '40px'}}  className="col-md-12"> <h1> {this.props.localidadFilter} </h1> </div>
                <div className="col-md-6"> <BarChart localidadFilter={this.props.localidadFilter} dateFilter={this.props.dateFilter} type='edad'/></div>
                <div className="col-md-6"> <PieChart localidadFilter={this.props.localidadFilter} dateFilter={this.props.dateFilter} /> </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
       User: state.reducer_user
    };
};

export default connect(mapStateToProps,null)(Graphic);
