// convert array into array of dictionaries
const convert=(data)=>{
    let array = []
    data.map((e) =>{
        let info = {}
        info['lat'] = e[1]
        info['lng'] = e[0]
        array.push(info)
        return null
    })
    return array
}


// order data for return global positions
const orderData =(data)=>{
    let array = []
    data.map( (e) =>{
        let info = {}
        info['0']= e['properties'].LocNombre
        let c = convert(e['geometry'].coordinates['0']['0'])
        info['1']= c
        array.push(info)
        return null
    })
    return array
}
